import React, { Component } from 'react'
import {withRouter} from "react-router"
import Myiscroll from "../../../common/myiscroll/myiscroll"
import { getArtical,getQuerytion} from "../../../../services/homeService" 

const Course =(props)=>(
    <div className="course">
    {props.course.map((item,index)=>(
    <li className="item border-bottom" key={index}>
        <img src={item.pic} alt="图片"/>
        <div className="right">
            <p>
                <span>{item.name}</span>
                {props.self&&<span
                className="tab self">原创</span>}
            </p>
            <p>
                 {item.title}
            </p>
            <div className="textflow">
               <span className="teacher">{item.teacher}</span>
                <p className="textflow">
                 { props.user && <span className="iconfont icon-yonghu"></span>} 
                { !props.user && <span className="iconfont icon-pinglun"></span>}
                   <span className="num"> {item.num}</span>
                </p>
            </div>
        </div>
</li>))}
    </div>
)

const Question = withRouter((props)=>(
    <div className="question">
      {  
      props.questions&&props.questions.map((item,index)=>(
        <p key={index} onClick={()=>props.history.push({pathname:"/home/search/question",state:{question:props.questions,id:item.id}})}>
         <span className="tab">{item.type}</span>
         <span className="textflow">{item.question}</span>
         <span className="iconfont icon-qianjin"></span>
       </p>
      )) 
        }
    </div>))

export default class result extends Component {
    constructor(props){
        super(props);
        this.state={
            navs:[
                {title:"课程",active:""},
                {title:"知识点",active:""},
                {title:"题库",active:""},
                {title:"文章",active:""}
            ],
            navsIndex:props.showIndex,
            course:[
                {
                    pic:"//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLAeFOermOI8uicV4rZibxKrroypuN5mbAyrUtPO3EovMzs9nGKexlhXUfribCNkcDh37Y/356?tp=webp",
                    name:"精讲课",
                    title:"第1课：概念于定义",
                    teacher:"张老师",
                    num:"9999"
                },
            ],
            knowledge:[
                {
                    pic:"//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCEB8zbbiaLSSymXZ8dMx95X1SLMCZYE9dWINGpMfCQJhxiaHTzl3mmWKGiaVp6vSmVLc/356?tp=webp",
                    name:"1、如何看待课程？",
                    title:"第一章",
                    teacher:"李老师",
                    num:"9999"
                },
            ],
            questions:[
                {
                    type:"选择",
                    answer: "1",
                    explains: "如图是中心圈，设在平面交叉路口的中心，用以区分车辆大、小转弯，及交叉路口车辆左右转弯的指示，车辆不得压线行驶。因此本题正确。",
                    id: "2",
                    item1: "正确",
                    item2: "错误",
                    question: "在这个路口左转弯要靠路口中心点左侧转弯。", 
                    url: "http://images.juheapi.com/jztk/subject4/2.jpg"
                },
            ],
            article:[
                {
                    pic:"//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCl1zfIxxnpJjdZ4picvNV8n7oYmJJZSQnEzSHITZCYtnNBylBnPDB6Wk0xUgYdVLj0/356?tp=webp",
                    name:"有哪些踩坑可以避免？",
                    title:"我们教你不踩坑",
                    num:"666" 
                },
            ]
        }
    }

    chageAction(index){
        let {navs,navsIndex} = this.state;
        navs = navs.map((item,ind)=>{
            if(ind===index){
              item.active = "active";
              navsIndex = index;
            }else{
              item.active = "";
            }
            return item;
        })
        this.setState({
            navs,
            navsIndex
        }); 
    }

    setAticle(data){
        let arr = data?data.map(item=>{
            let obj = {};
            obj.pic = item.img; 
            obj.name = item.title;
            obj.title = item.sub1;
            obj.num = parseInt(item.reading);
            return obj;
        }):[];
       this.setState({
        article:arr
       });
    }

    setQuestion(data){
        let question = data.map(item=>{
            item.type= "选择";
            return item;
        });
        this.setState({
            questions:question
        })
    }

    setCourse(){
        let arr =  [];
        for(let i=0;i<30;i++){
            let obj = {};
            obj.pic = "//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLAeFOermOI8uicV4rZibxKrroypuN5mbAyrUtPO3EovMzs9nGKexlhXUfribCNkcDh37Y/356?tp=webp";
            obj.name="精讲课";
            obj.title=`第${i+1}课：概念于定义${i+1}`
            obj.teacher = "张老师";
            obj.num = "999+";
            arr.push(obj);
        }
        this.setState({
            course:arr
        });
    }
  
    setKnowledge(){
        let arr =  [];
        for(let i=0;i<30;i++){
            let obj = {};
            obj.pic = "//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCEB8zbbiaLSSymXZ8dMx95X1SLMCZYE9dWINGpMfCQJhxiaHTzl3mmWKGiaVp6vSmVLc/356?tp=webp";
            obj.name=`${i+1}、如何看待课程？`;
            obj.title=`第${i+1}章`
            obj.teacher = "李老师";
            obj.num = "999+";
            arr.push(obj);
        }
        this.setState({
            knowledge:arr
        });
    }

    render() {
        let {navs,navsIndex,course,knowledge,questions,article} = this.state;

        return (
            <div className="searchResult">
                <div className="navbar border-bottom">
                    {
                     navs.map((item,index)=>(
                     <span key={index} 
                     ref={"item"+index}
                     className={item.active}
                     onClick={this.chageAction.bind(this,index)}>{item.title}</span>
                     ))
                    }
                </div>
                <Myiscroll top="48px">
                  { (navsIndex===0) && <Course user course={course}/>}
                  { (navsIndex===1) && <Course user course={knowledge}/>}
                  { (navsIndex===2) && <Question questions={questions} />}
                  { (navsIndex===3) && <Course self user={false} course={article}/>}
                </Myiscroll>
            </div>
        )
    }

    componentDidMount(){
        let index = this.state.navsIndex || 0;
        // 初始化选中列表
        this.chageAction(index);
        // 获得文章
        this.setCourse();
        // 获得知识点
        this.setKnowledge();
        // 获得文章
        getArtical(250)
        .then(data=>{
          this.setAticle(data.data);
        });
        // 获得题库
        getQuerytion()
        .then(data=>{
           this.setQuestion(data)
        })
    }
}

